import { getRealStrLenth, replaceStr, splitStringByLength, getNumberOfChines, getCharRatio } from '@assets/utils'

export declare interface Table2CommUtil {
  maxCellWidth: number;
  horiTemplate: string;
  verticalTemplate: string;
  quadrangleChar: string;
  tableData: string[][];
  genComment: () => CommData;
  setData: (data: string[][]) => void;
  getTableWH: () => number[][];
}

export class Table2Comm implements Table2CommUtil {
  public maxCellWidth: number //  
  public horiTemplate: string // 
  public verticalTemplate: string
  public quadrangleChar: string
  public tableData: string[][]

  constructor (maxCellWidth = 24, horiTemplate = '', verticalTemplate = '|', quadrangleChar = '+') {
    this.maxCellWidth = maxCellWidth
    this.horiTemplate = horiTemplate
    this.verticalTemplate = verticalTemplate
    this.quadrangleChar = quadrangleChar
  }

  setData (tableData: string[][]): void{
    this.tableData = tableData
  }

  setConfigData (key: string, val: string | number): void{
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    (this as any)[key] = val
  }

  genComment (): CommData {
    let horiDividerLine = '' // 
    const [colWidths, rowHeights] = this.getTableWH() //  
    const TABLE_WIDTH = colWidths.reduce((pre, cur) => pre + cur, 0) //  
    const templateLen = getRealStrLenth(this.horiTemplate) //  1 1.82
    // 
    for (let i = 0; i < TABLE_WIDTH; i++) {
      (i + 1) % templateLen === 0 && (horiDividerLine += this.horiTemplate)
    }
    horiDividerLine += this.quadrangleChar + '\n' // 
    //   
    const contentLine = this.verticalTemplate + (Array(TABLE_WIDTH).join(' ')) + this.verticalTemplate + '\n'

    let res = ''
    //   
    for (let i = 0; i < rowHeights.length; i++) {
      //    
      let writedLenth = 0
      //    
      const contentArr = new Array(rowHeights[i]).fill(contentLine)
      for (let j = 0; j < colWidths.length; j++) {
        // 
        // splitStringByLength 
        // 
        const rows = splitStringByLength(this.tableData[i][j], this.maxCellWidth)
        // contentArr.length 
        //     
        // 

        while (rows.length < contentArr.length) {
          rows.push(' ')
        }

        // 
        rows.forEach((t, index) => {
          const text = this.verticalTemplate + ' ' + t // t =>  padding
          const byteLen = getRealStrLenth(text) //  1 1.84
          // 
          const startIndex = writedLenth - getNumberOfChines(contentArr[index]) * (getCharRatio() - 1)
          contentArr[index] = replaceStr(contentArr[index], text, startIndex, byteLen + startIndex) // 
          if (i === 0) {
            horiDividerLine = replaceStr(horiDividerLine, this.quadrangleChar, startIndex, (startIndex + getRealStrLenth(this.quadrangleChar)))
          }
        })
        writedLenth = colWidths[j] + writedLenth //  
      }
      res += contentArr.join('') + horiDividerLine
    }
    // header 
    res = horiDividerLine + res
    return {
      comment: res,
      payload: TABLE_WIDTH
    }
  }

  getTableWH (): number[][] {
    const ROW_LEN: number = this.tableData.length // 
    const COL_LEN: number = this.tableData[0].length // 
    const colWidthArr: number[] = new Array(COL_LEN).fill(0) //  1 2 
    const rowHeightArr: number[] = new Array(ROW_LEN).fill(1) //  1 2

    // 
    for (let i = 0; i < ROW_LEN; i++) {
      for (let j = 0; j < COL_LEN; j++) {
        let strLength = getRealStrLenth(this.tableData[i][j]) // 
        if (strLength > this.maxCellWidth) { // 
          const height = Math.ceil(strLength / this.maxCellWidth) // 
          rowHeightArr[i] < height && (rowHeightArr[i] = height) //  
          strLength = this.maxCellWidth
        }
        strLength > colWidthArr[j] && (colWidthArr[j] = strLength) // 
      }
    }
    // padding   
    for (let i = 0; i < colWidthArr.length; i++) {
      colWidthArr[i] += 4
    }
    return [colWidthArr, rowHeightArr]
  }
}
